/**
 * External dependencies
 */
import { registerBlockType } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';
import { useBlockProps } from '@wordpress/block-editor';
import { Panel } from "@woocommerce/blocks-components";

/**
 * Internal dependencies
 */
import metadata from './block.json';

export const Edit = ( { attributes, setAttributes } ) => {
	const blockProps = useBlockProps();
	return (
		<>
			<style>
				{`
					.giftup-apply-panel {
						position: relative;
    					pointer-events: none;
					}
				`
				}
			</style>

			<div {...blockProps}>
				<div className="giftup-apply-panel components-disabled wc-block-components-totals-wrapper">
					<Panel
						className="wc-block-components-totals-coupon"
						title={__('Have a gift card?', 'gift-up')}
						titleTag="div"
						disabled={true}
					>
						</Panel>
				</div>
			</div>
		</>
	)
};

export const Save = ( { attributes } ) => {
	return (
		<div { ...useBlockProps.save() }>
		</div>
	);
};

registerBlockType(metadata.name, {
	icon: {
		src: (
			<svg id="Layer_1" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 205 205">
				<title>Mark Yellow Black</title>
				<polygon points="92.95 25.13 65.61 25.13 75.29 10.63 79.28 4.66 82.14 8.95 92.95 25.13" />
				<circle fill="#000" cx="102.54" cy="105.17" r="97.41" />
				<path fill="#fc0" d="M124.65,81.23H112.84v21.25h12.11c7.64,0,12.11-4.57,12.11-10.52v-.2C137.06,84.9,132.3,81.23,124.65,81.23Z" />
				<path fill="#fc0" d="M102.54,6.72A99.29,99.29,0,0,0,87.76,7.84L76.32.37,92.71,25h0l-.21-.61H86.93v82.3c0,21-11.81,31.37-30.48,31.37s-30.08-10.42-30.08-30.87V67.43H41.66v39.31c0,11.32,5.66,17.17,15,17.17s15-5.66,15-16.68V24.34H66.28l9.23-13.85a98.93,98.93,0,1,0,27-3.77Zm50,84.93c0,16.08-12.51,24.42-28.09,24.42H112.84v20.85H97.55V67.43h28.39c16.58,0,26.61,9.83,26.61,24Zm22.13,45.27H158.6V121h16.08Zm-3.87-23.63h-8.34l-4.67-43.88v-2h17.67v2Z" />
			</svg>
		),
		foreground: '#873FB9',
	},
	edit: Edit,
	save: Save
});
